<template>
	<view class="notice">
		<view v-if="list.length>0">
			<view class="mar-b-20" v-for="item in list" :key='item.carpool_id'>
				<needs :info='item'>
					<view class="btn btn-style glost" slot="btns" @click="refuse(item.carpool_id)">拒绝</view>
					<view class="btn btn-style mar-l-30" slot="btns" @click="agree(item.carpool_id)">同意</view>
				</needs>
			</view>
		</view>
		<noData v-else tip='暂无提醒' />
		<Modal title='请您确认是否接单' v-if='modalAgree' @change = 'changeAgree'></Modal>
		<Modal title='请填写拒绝理由' :btnCount='1' typeInput="textarea" :isInput="true" v-if='modalRefuse' @change = 'changeRefuse'></Modal>

	</view>
</template>

<script>
	import needs from '../../base/needs.vue'
	import MyApi from '../../apis/my.js'
	import noData from '../../base/nodata.vue'
	import Modal from '../../base/modal.vue'
	export default {
		name: 'notice',
		components: {
			needs,
			noData,
			Modal
		},
		data() {
			return {
				list: [],
				postData: {
					limit: 6,
					page: 1
				},
				total: 0,
				modalAgree:false,
				modalRefuse:false,
				modalId:null
			}
		},
		onLoad() {
			this.init()
		},
		methods: {
			init(list=[]) {
				MyApi.notice(this.postData).then(res => {
					this.total = res.total_page;
					this.list = list.concat(res.list)
				})
			},
			refuse(id){
				this.modalRefuse = true
				this.modalId = id
			},
			agree(id){
				this.modalAgree = true
				this.modalId = id
			},
			changeRefuse(flag,refuse_desc){
				if(refuse_desc==''){
					this.$util.showToast('请输入拒绝理由')
					return
				}
				let data = {
					carpool_id:this.modalId,
					refuse_desc
				}
				MyApi.noticeRefuse(data).then(res=>{
					this.modalRefuse = false
					this.$util.showToast('已拒绝','success')
					setTimeout(()=>{
						uni.navigateBack()
					},1000)
				})
			},
			changeAgree(flag){
				if(flag == 'SURE'){
					MyApi.noticeAgree({carpool_id:this.modalId}).then(res=>{
						this.modalAgree = false
						this.$util.showToast('已同意','success')
						setTimeout(()=>{
							uni.redirectTo({
								url:'/pages/order/index?index=1&type=1'
							})
						},1000)
					})
				}else{
					this.modalAgree = false
				}
			}
		},
		onReachBottom() {
			this.postData.page = this.postData.page + 1
			if (this.postData.page > this.total) {
				this.$util.showToast('没有更多了~')
			} else {
				this.$nextTick(()=>{
					this.init(this.list)
				})
			}
		}
	}
</script>

<style lang="less" scoped>
	.notice {
		padding: 20rpx 30rpx;
	}
</style>
